function calend() {
  const calendar = document.querySelector(".js-startdate");
  let thisMonth = new Date().getMonth();
  console.log(thisMonth);
  if (calendar) {
    flatpickr(calendar, {
      inline: true,
      static: true,
      showMonths: 1,
      plugins: [new rangePlugin({ input: ".js-enddate" })],
    });
  }
}

calend();
